<template>
  <v-container fluid fill-height>
    <v-layout row wrap align-center justify-center>
      <v-flex xs12 hidden-sm-and-up style="margin-top:90%;">blah</v-flex>
      <v-flex xs12 sm5>
        <v-card class="elevation-18 rounded-card">
          <v-layout row wrap fill-height>
            <v-flex class="rounded-card">
              <v-card flat class="rounded-card pa-4">
                <v-card-title class="justify-center">
                  <h1 style="color:#2E7395;">Registration Form</h1>
                </v-card-title>
                <v-layout pa-4 column>
                  <v-flex xs12>
                    <v-form v-model="valid" ref="form">
                      <v-text-field
                        v-model="name"
                        prepend-inner-icon="person"
                        :rules="nameRules"
                        :counter="10"
                        validate-on-blur
                        label="Name"
                        required
                      ></v-text-field>
                      <v-text-field
                        label="E-mail"
                        prepend-inner-icon="email"
                        v-model="email"
                        :rules="emailRules"
                        required
                      ></v-text-field>
                      <v-text-field
                        label="Phone Number"
                        prepend-inner-icon="phone"
                        v-model="phone"
                        :rules="phoneRules"
                        required
                      ></v-text-field>
                      <v-text-field
                        v-model="password"
                        prepend-inner-icon="lock"
                        :append-icon="show ? 'visibility' : 'visibility_off'"
                        :rules="passwordRules"
                        :type="show ? 'text' : 'password'"
                        validate-on-blur
                        name="input-10-1"
                        label="Password"
                        hint="At least 8 characters"
                        counter
                        @click:append="show = !show"
                      ></v-text-field>

                      <v-checkbox
                        v-model="checkbox"
                        label="I am not a Robot!"
                        :rules="[v => !!v || 'You must agree to continue!']"
                        required
                      ></v-checkbox>
                      <v-layout row>
                        <v-flex xs12>
                          <v-btn
                            block
                            color="#255F7C"
                            small
                            dark
                            @click="submit"
                            >Create Account</v-btn
                          >
                        </v-flex>
                      </v-layout>
                    </v-form>
                  </v-flex>
                </v-layout>
                <v-card-actions>
                  <span style="color:#255F7C;">Already have an account?</span>
                  <v-btn small flat type="link" to="/signin">
                    <u style="color:#0061FF;">Sign in</u>
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-flex>
          </v-layout>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      valid: false,
      show: false,
      success: false,
      password: "",
      passwordRules: [
        v => !!v || "Password is required",
        v => (v && v.length >= 8) || "Min 8 characters"
      ],
      name: "",
      nameRules: [
        v => !!v || "Name is required",
        v => (v && v.length <= 10) || "Name must be less than 10 characters"
      ],
      checkbox: "",
      email: "",
      emailRules: [
        v => !!v || "E-mail is required",
        v =>
          /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) ||
          "E-mail must be valid"
      ],
      phone: "",
      phoneRules: [
        v => !!v || "Phone number is required",
        v =>
          /(?=.*?\d{3}( |-|.)?\d{4})((?:\+?(?:1)(?:\1|\s*?))?(?:(?:\d{3}\s*?)|(?:\((?:\d{3})\)\s*?))\1?(?:\d{3})\1?(?:\d{4})(?:\s*?(?:#|(?:ext\.?))(?:\d{1,5}))?)\b/gi.test(
            v
          ) || "Phone number must be valid"
      ]
    };
  },
  methods: {
    submit() {
      if (this.$refs.form.validate()) {
        this.createUser();
      }
    },
    createUser() {
      // this will find the user from the localstorage or prefilled JSON
      this.$store
        .dispatch("createUser", { name: this.name, password: this.password })
        .then(res => {
          this.success = res;
          this.$router.push({ path: "/landing" });
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>
<style scoped lang="scss">
.rounded-card {
  border-radius: 50px;
}
.round-left {
  border-radius: 50px 0px 0px 50px;
}
.round-right {
  border-radius: 0px 50px 50px 0px;
}
</style>
